import React from "react";
import classes from "./Education.styl";
import Content from "../Content";

const Education = props => {
  return (
    <div className={classes.root}>
      <div className={classes.subTitle}>{props.title}</div>
      <div className={classes.period}>{props.degree}</div>
      <div className={classes.position}>{props.period}</div>
      <div>
        {props.content.map((p, i) => (
          <Content key={i} {...p} />
        ))}
      </div>
    </div>
  );
};

export default Education;
