import React from "react";
import classes from "./Skill.styl";

const Skill = props => {
  return (
    <span className={classes.root}>
      <span className={classes.title}>{props.title}</span>
      <div className={classes.stars}>{renderStars(props.level)}</div>
    </span>
  );
};

function renderStars(level) {
  let stars: any = [];
  for (let s = 0; s < 5; s++) {
    if (s < level) {
      stars.push(<span key={s} className={classes.star} />);
    } else {
      stars.push(<span key={s} className={classes.inactiveStar} />);
    }
  }
  return stars;
}

export default Skill;
