import React, { Component } from "react";
import { hot } from "react-hot-loader";
import "roboto-fontface/css/roboto/roboto-fontface.css";
import classes from "./App.styl";
import data from "../data";
import Section from "./Section";
import { Skill, WorkExperience, Education, Contact } from "./SectionDataTypes";
import Profile from "./Profile";

class App extends Component {
  render() {
    return (
      <div className={classes.root}>
        <Profile {...data.profile} />
        {data.sections.map((section, i) => (
          <Section key={i} title={section.title}>
            {this.renderSectionData(section)}
          </Section>
        ))}
      </div>
    );
  }

  renderSectionData(section) {
    switch (section.type) {
      case "text":
        return section.data.map((p, i) => <p key={i}>{p}</p>);

      case "Skill":
        return section.data.map((skill, i) => <Skill key={i} {...skill} />);

      case "WorkExperience":
        return section.data.map((workExperience, i) => (
          <WorkExperience key={i} {...workExperience} />
        ));

      case "Education":
        return section.data.map((education, i) => (
          <Education key={i} {...education} />
        ));

      case "Contact":
        return section.data.map((contact, i) => (
          <Contact key={i} {...contact} />
        ));

      default:
        return null;
    }
  }
}

export default hot(module)(App);
