import React from "react";
import classes from "./WorkExperience.styl";
import Content from "../Content";

const WorkExperience = props => {
  return (
    <div className={classes.root}>
      <div className={classes.subTitle}>{props.company}</div>
      <div className={classes.period}>{props.role}</div>
      <div className={classes.position}>{props.period}</div>
      <div>
        {props.content.map((p, i) => (
          <Content key={i} {...p} />
        ))}
      </div>
      <div className={classes.techs}>
        {props.techs.map((t, i) => (
          <span key={i} className={classes.tech}>
            {t}
          </span>
        ))}
      </div>
    </div>
  );
};

export default WorkExperience;
