import React from "react";
import classes from "./Content.styl";

const Content = props => {
  switch (props.type) {
    case "text":
      return <p className={classes.p}>{props.content}</p>;

    case "span":
      return <span>{props.content}</span>;

    case "bullet":
      return <p className={classes.bullet}>{props.content}</p>;

    case "link":
      return (
        <a className={classes.link} href={props.href}>
          {props.content}
        </a>
      );

    default:
      return null;
  }
};

export default Content;
